/* Global Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; /* Prevents layout shift issues */
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
}

html, body {
    width: 100%;
    overflow-x: hidden; /* Prevents extra horizontal scrolling */
}
body {
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
/* Layout */
.whole {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

nav {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 25px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1;
}

nav .logo {
    width: 90px;
}

nav ul {
    display: flex;
}

nav ul li {
    padding: 10px;
    list-style: none;
    margin-left: 20px;
}

nav ul li a {
    text-decoration: none;
    color: #ffffff;
    font-size: 25px;
}

nav ul li a:hover {
    color: #c7c7c7;
}

/* Image Container */
.image-container {
    position: relative;
    filter: brightness(40%);
    width: 100%;
    overflow: hidden;
}

.image-container img {
    object-fit: cover;
    max-height: 400px;
    width: 100%;
}

/* Heading */
.heading {
    position: absolute;
    color: #ffffff;
    top: 21%;
    left: 30%;
    font-size: 40px;
    font-weight: 500px;
    display: flex;
    align-items: center;
}

.heading h2 {
    margin-left: 0; /* Remove unnecessary margin */
}
/* Content Container */
.container {
    max-width: 80%;
    margin: auto; /* Centering */
    padding: 20px;
}

/* Highlight Box */
.highlight-box {
    font-size: 20px;
    background-color: #d9f2ff;
    padding: 15px;
    border-radius: 5px;
}

.title {
    font-size: 30px;
    margin-bottom: 10px;
}

.itenary h3 {
    font-size: 25px;
}

.highlight-box p {
    font-size: 21px;
}

/* Itinerary Day Styles */
.day {
    background: white;
    padding: 5px;
    border-left: 5px solid blue;
}

.day h4 {
    color: #000000;
    font-size: 29px;
    padding: 20px;
}

.day p {
    color: #4b4a4a;
    font-size: 24px;
    padding: 15px;
}

/* Sidebar Box Styles */
.box {
    padding: 15px;
    color: white;
    border-radius: 5px;
}

.box-red, .box-orange, .box-green {
    font-size: 30px;
    height: 360px;
    margin-top: 10px;
    border-radius: 12px;
}

.box-red h3, .box-orange h3, .box-green h3 {
    font-size: 30px;
    margin-left: 20px;
    margin-top: 40px;
}

.box-red p, .box-orange p, .box-green p {
    font-size: 25px;
}

/* Sidebar Colors */
.box-red {
    background-color: #d9534f;
}

.box-orange {
    background-color: #f0ad4e;
}

.box-green {
    background-color: #5cb85c;
}

/* Footer */
footer {
    background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), 
                url('images/dal lake.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 300px;
    font-size: 25px;
    margin-top: 60px;
    display: flex;
    justify-content: space-around;
    color: #fff;
    padding: 30px;
}

footer ul {
    list-style-type: none;
}

footer ul li {
    margin-bottom: 8px;
}

.column {
    width: 27%;
}

p {
    font-size: 30px;
    font-weight: 400;
    margin-bottom: 15px;
}

/* Responsive Design */
@media screen and (max-width: 1024px) {
    .heading {
        font-size: 30px;
        top: 15%;
        left: 15%;
    }

    .container {
        max-width: 90%;
        margin: auto;
    }

    .whole {
        flex-direction: column;
    }
}

@media screen and (max-width: 768px) {
    nav {
        flex-direction: column;
        align-items: center;
        padding: 10px;
    }

    nav ul {
        flex-direction: column;
        align-items: center;
        margin-top: 10px;
    }

    nav ul li {
        margin: 10px 0;
    }

    .heading {
        font-size: 25px;
        left: 10%;
        top: 10%;
        text-align: center;
    }

    .container {
        max-width: 95%;
    }

    .image-container img {
        max-height: 300px;
    }

    .day h4 {
        font-size: 24px;
        padding: 10px;
    }

    .day p {
        font-size: 20px;
        padding: 10px;
    }

    .box-red, .box-orange, .box-green {
        font-size: 24px;
        height: auto;
        padding: 15px;
    }

    .box-red h3, .box-orange h3, .box-green h3 {
        font-size: 28px;
    }

    .box-red p, .box-orange p, .box-green p {
        font-size: 22px;
    }
}

@media screen and (max-width: 480px) {
    .heading {
        font-size: 20px;
        left: 5%;
        top: 5%;
        text-align: center;
    }

    nav ul li a {
        font-size: 20px;
    }

    .container {
        padding: 15px;
    }

    .day h4 {
        font-size: 22px;
        padding: 8px;
    }

    .day p {
        font-size: 18px;
        padding: 8px;
    }

    .box-red, .box-orange, .box-green {
        font-size: 20px;
        height: auto;
        padding: 10px;
    }

    .box-red h3, .box-orange h3, .box-green h3 {
        font-size: 26px;
    }

    .box-red p, .box-orange p, .box-green p {
        font-size: 20px;
    }
}

@media (max-width: 1045px) { 
    .card {
        margin: 10px 0;
        width: calc(50% - 10px);
    }
}

@media (max-width: 700px) {
    .card {
        width: 100%;
    }
}
